export function typeMasterFormatter(cell) {
    switch (cell) {
        case 'TOP_LEADER':
            return "  ";
        case 'TOP':
            return " ";
        case 'MIDDLE_PLUS':
            return "  ";
        case 'MIDDLE':
            return " ";
        case 'START':
            return " ";
        case 'SUPPORT':
            return "";
        case 'TOP_COLORIST':
            return " ";
        case 'COLORIST':
            return "";
        case 'CARE_MASTER':
            return "   ";
        default:
            return "";
    }
}

export function typeHairFormatter(cell) {
    switch (cell) {
        case 'HAIR_EXTENSION':
            return " ";
        case 'HAIR_REMOVAL':
            return " ";
        default:
            return "";
    }
}

export function typeMAsterWorkingDayFormatter(cell) {
    switch (cell) {
        case '$2X2':
            return "2  2";
        case '$5X2':
            return "5  2";
        case '$4X2':
            return "4  2";
        default:
            return "";
    }
}
