import React from "react";

function  maxLength(cell) {
    let max = cell !== 0 ? cell : "";
    return (
        <span>{max}</span>
    );
}

export default [
    {
        dataField: 'minLength',
        text: ' '
    },
    {
        dataField: 'maxLength',
        text: ' ',
        formatter: maxLength
    },
    {
        dataField: 'price',
        text: '  '
    }
]